class AboutBox extends PureComponent {

    state = {
        kidzoo: {
          profileImg: kxxzxxArtist
        }
        , bkkoli: {
          profileImg: "https://instagram.ficn1-1.fna.fbcdn.net/vp/499af9c03c4c1c29df84ca39c8156fc2/5DA06B90/t51.2885-19/s320x320/26873048_732739253590947_1331259138126118912_n.jpg?_nc_ht=instagram.ficn1-1.fna.fbcdn.net"
        }, cin: {
          profileImg: "https://instagram.ficn1-1.fna.fbcdn.net/vp/56f20eabc2ba79f24b8731225e3ccc6a/5DB24E4E/t51.2885-19/s320x320/59535953_288494515428274_1713225994397024256_n.jpg?_nc_ht=instagram.ficn1-1.fna.fbcdn.net"
        } , Omondi: {
            profileImg: OmondiProfile
        } , DA: {
            profileImg: daArtist
        }
    }

    render() {
        return (
            <>
            <div className="subtitle">
                # What is the indazoo?
            </div>
            <div id="aboutBox" className="commonBox">
                <div className="leftCommonBox">
                    <div className="leftCommonFirstBox">
                        <div className="logoImg">
                            <Image className="instaShapeLogo" src={indazooFaceLogo} roundedCircle />
                        </div>
                        <div className="logoName">
                            Indazoo
                        </div>
                    </div>
                    <div className="leftCommonSecondBox">
                        <div className="memberImg">
                            <Image src={indazooAllMember} rounded/>
                        </div>
                    </div>
                </div>
                <div className="rightCommonBox">
                    <div className="indazooMember">
                        <div className="rightName" style={{paddingLeft:17 + 'px'}}>
                            Member
                        </div>
                    </div>
                    <div className="indazooMember">
                        <div className="rightlogoImg">
                            <Image className="rightLogo" src={this.state.kidzoo.profileImg} roundedCircle/>
                        </div> 
                        <div className="rightName">
                            <NavLink className="homeMenu" onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave} exact to="/artist/kxxzxx" >
                                kxxzxx
                            </NavLink>
                        </div>
                    </div>
                    <div className="indazooMember">
                        <div className="rightlogoImg">
                            <Image className="rightLogo" src={this.state.DA.profileImg} roundedCircle/>
                        </div> 
                        <div className="rightName">
                            <NavLink className="homeMenu" onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave} exact to="/artist/DA" >
                                D.A
                            </NavLink>
                        </div>
                    </div>
                    <div className="indazooMember">
                        <div className="rightlogoImg">
                            <Image className="rightLogo" src={this.state.Omondi.profileImg} roundedCircle/>
                        </div> 
                        <div className="rightName">
                            <NavLink className="homeMenu" onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave} exact to="/artist/Omondi" >
                                Omondi
                            </NavLink>
                        </div>
                    </div>
                    <div className="indazooMember">
                        <div className="rightlogoImg">
                            <Image className="rightLogo" src={this.state.cin.profileImg} roundedCircle/>
                        </div> 
                        <div className="rightName">
                            <NavLink className="homeMenu" onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave} exact to="/artist/cin" >
                                c-in
                            </NavLink>
                        </div>
                    </div>
                    <div className="indazooMember">
                        <div className="rightlogoImg">
                            <Image className="rightLogo" src={this.state.bkkoli.profileImg} roundedCircle/>
                        </div> 
                        <div className="rightName">
                            <NavLink className="homeMenu" onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave} exact to="/artist/bkkoli" >
                                bkkoli
                            </NavLink>
                        </div>
                    </div>
                </div>
            </div>
            </>
        )
    }
}

